import React from 'react';
import { Collapse,Select,Spin} from 'antd';
const { Panel } = Collapse;
const {Option} = Select;
import DraggableFactCard from './DraggableFactCard';
import OperationType from '@/constant/OperationType'
import Fact from '@/model/fact';
import config from '@/axios/config';
import uuidv4 from 'uuid/v4';
import { filter } from 'lodash';
import * as api from '@/axios/api';
import activitiesCode from '@/axios/activitiesCode';
import record_activity from '@/axios/recordactivities'
import { animationFlag } from '../../../selector/story';
import { changeToAnimation } from '../../../action/storyAction';

import { genFactSentence,genSubtitle} from '../../../sentencer/index';
import "./index.less";
const addFactNum = 10;
var settledTimeOut = 0;
var loopOrNot = false;
//var animationType = this.props.animationType;
var animationType = ""
var animateOrNot = false;
export default class MoreFactsSelections extends React.Component{
    constructor(props) {
        super(props);
        let lang = props.intl.options.currentLocale
        filter(lang);
        this.state = {
            lang: lang,
        }
    }
    componentDidMount() {
        const { unusedFacts } = this.props;

        if (!unusedFacts.length) {
            this.addFact();
        }
      
    }
    componentDidUpdate() {
        
    }

    addFact = () => {
        this.setState({
            operateState: OperationType.ADDING
        })
        //TODO:fact
        this.adding(config.url.generateRandomlyNew).then(response => {
            const facts = response.data.story.facts;
            let tempFacts = [];
            for (let factDict of facts) {
                let fact = new Fact(
                    factDict['type'],
                    factDict['measure'],
                    factDict['subspace'],
                    factDict['groupby'],
                    factDict['focus'],
                    factDict['parameter'],
                    "", // chart
                    factDict['score'],
                    factDict['information'],
                    factDict['significance']
                );
                fact.uuid = uuidv4();
                tempFacts.push(fact);
            }
            this.props.addFact(tempFacts);
            
        });
    }

    adding = (url) => {
        return new Promise(async (resolve, reject) => {
            let fileName = this.props.fileName;
            let filters = _.cloneDeep(this.props.filtersLocal);
     
            let data;
            if(filters)
            data= {
                file_name: fileName,
                max_story_length: addFactNum,
                topic_filters: JSON.stringify(filters),
                // method: 'mcts'
            }
            else{
                data= {
                    file_name: fileName,
                    max_story_length: addFactNum,
                    //filters: JSON.stringify(filters),
                    // method: 'mcts'
                }
            }
         
            const response = await api.generate(url, data)
            console.log('response-adding', response)
            if (response.data.fail) {
                message.info(this.props.intl.get("cannot interpolate."))
                this.setState({
                    operateState: OperationType.ADD_FAILED,
                    errorMessage: response.data.fail
                })
                reject()
                record_activity(activitiesCode.generateMoreFacts, 0 , {filename:fileName, response: response});
                return;
            }
            if (response.data.error) {
                this.setState({
                    operateState: OperationType.ADD_FAILED,
                    errorMessage: response.data.error
                })
                reject()
                record_activity(activitiesCode.generateMoreFacts, 0 , {filename:fileName, response: response});
                return;
            }
            this.setState({
                operateState: OperationType.GENERATED
            })
            resolve(response)
            record_activity(activitiesCode.generateMoreFacts, 1 , {filename:fileName, response: response});
        })
    }

     // 
     staticChartsShow = () => {
        //return new Promise(async (resolve, reject) => {
            
            for(let i =0;i<=settledTimeOut;i++){
                clearTimeout(i);
            }
            settledTimeOut = 0;

            let tempFacts = [];

            for (let factDict of this.props.facts) {
                
                let fact = new Fact(
                    factDict['type'],
                    factDict['measure'],
                    factDict['subspace'],
                    factDict['groupby'],
                    factDict['focus'],
                    factDict['parameter'],
                    factDict['chart'],//chart
                    factDict['score'],
                    factDict['information'],
                    factDict['significance']
                )
                fact.generatedScript = factDict.generatedScript; //edit script
                fact.duration = factDict.duration;//save duration
                tempFacts.push(fact);
            }

            this.props.generateStory(tempFacts.slice(), [
                 "none",
                "similarity",
                "similarity",
                 "similarity",
                "similarity",
                 "similarity"
             ], 1);

            this.setState({
                operateState: OperationType.GENERATED
            })
            this.props.isUpdateEditing(false)
            
            console.log("")
            // 
        //})
    }


    // 
    playInSequence = () => {
        
        if(animateOrNot && animationType == "")
        {
            let seuqenceLentgh = this.props.facts.length;

            for(let i =0;i<=settledTimeOut;i++){
                clearTimeout(i);
            }
            settledTimeOut = 0;

            let tempFacts = [];
        
            let updatedFact = 0;
            for (let factDict of this.props.facts) {
                

                let fact = new Fact(
                    factDict['type'],
                    factDict['measure'],
                    factDict['subspace'],
                    factDict['groupby'],
                    factDict['focus'],
                    factDict['parameter'],
                    factDict['chart'],//chart
                    factDict['score'],
                    factDict['information'],
                    factDict['significance']
                )
                fact.generatedScript = factDict.generatedScript; //edit script
                fact.duration = factDict.duration;//save duration
                tempFacts.push(fact);
                // factstate
                if(updatedFact==0)//updatefact
                {
                    if(this.props.facts.length<seuqenceLentgh) //factfact
                    {
                        for(let i =0;i<=settledTimeOut;i++){
                            clearTimeout(i);
                        }
                        settledTimeOut = 0;
                        this.playInSequence();
                        return;
                    }
                    else
                    {
                        if(this.props.facts.length>seuqenceLentgh) //fact
                        {
                            for(let i =0;i<=settledTimeOut;i++){
                                clearTimeout(i);
                            }
                            return;
                        }
                        else
                        {
                            this.props.updateFact(updatedFact, fact);
                            console.log("first fact"+updatedFact+"-------------------------------");
                            if(updatedFact==this.props.facts.length-1)//facts
                                {
                                    if(animateOrNot &&loopOrNot && animationType == "")
                                    {
                                        settledTimeOut = setTimeout(()=>{// loop
                                            
                                            this.playInSequence();
                                        },(tempFacts[updatedFact].duration+1000));
                                    }
                                }
                        }
                    }
                }
                else{//updatefact
                    
                    let factNow = _.cloneDeep(fact);
                    let updatedFactNow = updatedFact;
                    let waitingTime = function(){//fact
                        let totalWaitingTime = 0;
                        for(let j=0;j<updatedFactNow;j++){
                            totalWaitingTime += tempFacts[j].duration;//factduration
                        }
                        return totalWaitingTime;
                    }
                    settledTimeOut = setTimeout(()=>{//factupdate
                        if(this.props.facts.length<seuqenceLentgh) //factfact
                        {
                            for(let i =0;i<=settledTimeOut;i++){
                                clearTimeout(i);
                            }
                            settledTimeOut = 0;
                            this.playInSequence();
                            return;
                        }
                        else
                        {
                            if(this.props.facts.length>seuqenceLentgh) //fact
                            {
                                for(let i =0;i<=settledTimeOut;i++){
                                    clearTimeout(i);
                                }
                                return;
                            }
                            else
                            {
                                this.props.updateFact(updatedFactNow, factNow);// 
                                if(updatedFactNow==this.props.facts.length-1)
                                {
                                    if(animateOrNot &&loopOrNot && animationType == "")
                                    {
                                        settledTimeOut = setTimeout(()=>{// loop
                                            
                                            this.playInSequence();
                                        },(tempFacts[updatedFactNow].duration)+1000);
                                    }
                                }
                            }
                        }
                    },waitingTime()+1000);
                    console.log("fact: "+updatedFact+"-------------------------------");
                    console.log("facts length: "+ this.props.facts.length+"-------------------------------")
                }
                
                updatedFact++;
            }
            console.log("")
            // 
        }
    }


    // 
    playTogether = () => {
        
        if(animateOrNot && animationType == "")
        {
            let togetherlength = this.props.facts.length;
            

            for(let i =0;i<=settledTimeOut;i++){
                clearTimeout(i);
            }
            settledTimeOut = 0;

            let tempFacts = [];
            let longestWaitingTime = 0;

            for (let factDict of this.props.facts) {
                
                let fact = new Fact(
                    factDict['type'],
                    factDict['measure'],
                    factDict['subspace'],
                    factDict['groupby'],
                    factDict['focus'],
                    factDict['parameter'],
                    factDict['chart'],//chart
                    factDict['score'],
                    factDict['information'],
                    factDict['significance']
                )
                fact.generatedScript = factDict.generatedScript; //edit script
                fact.duration = factDict.duration;//save duration
                tempFacts.push(fact);

                if(longestWaitingTime<fact.duration)// duration
                {
                    longestWaitingTime = fact.duration
                }
            }
            
            this.props.generateStory(tempFacts.slice(), [
                 "none",
                "similarity",
                "similarity",
                 "similarity",
                "similarity",
                 "similarity"
             ], 1);

             if(animateOrNot && loopOrNot && animationType == "")
             {
                 settledTimeOut = setTimeout(()=>{// loop
                    if(this.props.facts.length>togetherlength) //fact
                    {
                        for(let i =0;i<=settledTimeOut;i++){
                            clearTimeout(i);
                        }
                        return;
                                    
                    }
                    else
                    {
                        if(this.props.facts.length<togetherlength)
                        {
                            for(let i =0;i<=settledTimeOut;i++){
                                clearTimeout(i);
                            }
                            this.playTogether();
                            return;
                        }
                        else
                        {
                            if(loopOrNot)
                            {this.playTogether();}
                        }
                    }
                },(longestWaitingTime+1000));
             }

            
            console.log("")
            // 
        }
    }

    render() {
        const { intl, initDone,unusedFacts} = this.props;
        const { operateState, lang, dataSource } = this.state;
        let tip = initDone && intl.get("adding");
        unusedFacts.map((item, index) => {
            item.generatedScript = genFactSentence(item, lang)
            item.generatedSubtitle = genSubtitle(item, lang)
        })
        const AddView = () => {
            return (
                <div className='add'>
                    <div className="addImage" onClick={this.addFact}></div>
                </div>
            )
        }
        return(
            <Collapse  defaultActiveKey={['datafacts1']}>
                <Panel header={initDone && intl.get("Data Fact")} key="datafacts1">
                    <div className="DataFacts">
                        <Spin tip={tip} spinning={operateState === OperationType.ADDING} wrapperClassName="editSpining">
                            {/* <DataFactsTxt /> */}
                            <div className='factsList'>
                                <ul>
                                    {
                                        unusedFacts.map((fact, index) => {
                                            return <DraggableFactCard key={fact.uuid + fact.index} dataresLocal = {this.props.dataresLocal} schemaFilterLocal = {this.props.schemaFilterLocal}  {...this.props} fact={fact} index={index} id={fact.uuid} isReady={true} animationFlag={animationFlag} changeToAnimation={changeToAnimation} onChange={this.onChange} animationType={animationType} staticChartsShow={this.staticChartsShow} playInSequence={this.playInSequence} playTogether={this.playTogether}/>
                                            
                                        })
                                    }
                                </ul>
                            </div>
                        </Spin>
                    </div>
                    <div>
                        <AddView />
                    </div>
                </Panel>
            </Collapse>
        )
    }
} 